export enum TrackType {
    video = 'video',
    audio = 'audio'
}

/**
 * 
 */
export type Track = {
    id: number;
    type: TrackType;
    codec: string;
    defaultCodec?: string;
    timescale: number;
    duration: number;
    samples: Sample[];
    mp4Samples: MP4Sample[];
    inputTimescale: number;
    sequenceNumber: number;
    sampleDuration: number;
}

/**
 * 
 */
export type AudioTrack = {
    samples: AudioSample[];
    mp4Samples: MP4AudioSample[];
    config: number[];
    samplerate: number;
    channelCount: number;
} & Track;

/**
 * 
 */
export type VideoTrack = {
    samples: VideoSample[];
    mp4Samples: MP4VideoSample[];
    width: number;
    height: number;
    codecWidth: number;
    codecHeight: number;
    sps: Uint8Array[];
    pps: Uint8Array[];
    pixelRatio: number[];
    profile: string;
    level: string;
    chromaFormat: string;
    fps: number;
} & Track;

/**
 * demuxsample
 */
export type Sample = {
    pts: number;
    dts: number;
    streamDTS: number;
}

/**
 * demuxsample
 */
export type AudioSample = {
    unit: Uint8Array;
} & Sample;

/**
 * demuxsample
 */
export type VideoSample = {
    key: boolean;
    cts: number;
    units: Uint8Array[];
} & Sample;

/**
 * mp4sample
 */
export type MP4Sample = {
    cts: number,
    len: number,
    duration: number,
    streamDTS: number,
    flags: {
        isLeading: number,
        isDependedOn: number,
        hasRedundancy: number,
        degradPrio: number,
        dependsOn: number,
        isNonSync: number,
    }
};

/**
 * mp4sample
 */
export type MP4AudioSample = {
    unit: Uint8Array;
} & MP4Sample;

/**
 * mp4sample
 */
export type MP4VideoSample = {
    units: Uint8Array[];
} & MP4Sample;

/**
 * remuxMP4segment
 */
export type MP4Segment = {
    payload: Uint8Array,
    startPTS: number,
    endPTS: number,
    startDTS: number,
    endDTS: number,
    type: TrackType,
    streamDTS: number
};

/**
 * remux
 */
export type MP4RemuxResult = {
    segments: MP4Segment[],
    extra: any;
}
