/**
 * flv
 * @param url 
 * @param spts 0pts0
 */
export function abrGetUrl(url: string, spts?: number): string {
    if (typeof spts === 'undefined') {
        return url;
    }
    let arr = url.split('?');
    arr.splice(1, 0, `?startPts=${spts}${arr.length > 1 ? '&' : ''}`);
    return arr.join('');
}