/**
 * las.js
 */
export enum ErrorTypes {
    // 
    NETWORK_ERROR = 'networkError',
    // video
    MEDIA_ERROR = 'mediaError',
    // 
    MUX_ERROR = 'muxError',
    // 
    OTHER_ERROR = 'otherError',
    // MSE
    MSE_ERROR = 'mseError'
};

/**
 * las.js
 */
export enum ErrorDetails {
    // 
    LOAD_ERROR = 10,
    // 
    LOAD_ERROR_TIMEOUT = 11,
    // HTMLMediaElement
    VIDEO_ERROR = 101,
    // 
    UNSUPPORTED = 102,
    // 
    CONFIG_ERROR = 103,
    // 
    MANIFEST_ERROR = 104,
    // video
    NO_VIDEO = 105,
    // mediaSourceError MSEMediaSource
    MEDIASOURCE_ERROR = 200,
    // addSourceBufferError MSESourceBuffer
    ADDSOURCEBUFFER_ERROR = 201,
    // sourceBufferError MSESourceBuffer
    SOURCEBUFFER_ERROR = 202,
    // endOfStreamError MSE
    ENDOFSTREAM_ERROR = 203,
    // appendBufferError MSEbuffer
    APPENDBUFFER_ERROR = 204,
    // 
    DEMUX_ERROR = 301,
    // 
    REMUX_ERROR = 302,
    // alloc mdat
    REMUX_ALLOC_ERROR = 303,
};

/**
 * las.js
 */
export type ErrorData = {
    type: ErrorTypes,
    details: ErrorDetails,
    fatal: boolean,
    info: {
        reason: string,
        url?: string,
        httpStatusCode?: number
    }
}